.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.header-box {
  background-color: #FFFDD0; 
  color: darkolivegreen; 
  padding: 20px;
  text-align: center;
  font-family: 'BellBottom.Laser', sans-serif;
  font-size: 22px;
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #FFFDD0;
  padding: 20px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
